.main-nav--closed {
  display: none;
}

.main-nav--opened,
.main-nav--no-js {
  display: block;
}

@media (min-width: 960px) {
  .main-nav {
    width: 620px;

    display: flex;
    justify-content: space-between;
  }

  .main-nav__wrapper {
    width: 640px;

    display: flex;
    justify-content: space-between;
  }
}
